body {
  font: normal 16px/1.5 "Helvetica Neue", sans-serif;
  background: #456990;
  color: #fff;
  overflow-x: hidden;
  padding-bottom: 50px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* TIMELINE
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.timeline ul {
  background: #1d1124;
}

.timeline ul li {
  list-style-type: none;
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;
  background: #fff;
}

.timeline ul li::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: inherit;
}

.timeline ul li div {
  position: relative;
  bottom: 0;
  width: 400px;
  padding: 15px;
  background: #3B2349;
}

.timeline ul li div::before {
  content: '';
  position: absolute;
  bottom: 7px;
  width: 0;
  height: 0;
  border-style: solid;
}

.timeline ul li:nth-child(odd) div {
  left: 45px;
}

.timeline ul li:nth-child(odd) div::before {
  left: -15px;
  border-width: 8px 16px 8px 0;
  border-color: transparent #3B2349 transparent transparent;
}

.timeline ul li:nth-child(even) div {
  left: -439px;
}

.timeline ul li:nth-child(even) div::before {
  right: -15px;
  border-width: 8px 0 8px 16px;
  border-color: transparent transparent transparent #3B2349;
}

time {
  display: block;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 8px;
}


/* EFFECTS
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.timeline ul li::after {
  transition: .5s ease-in-out;
}

.timeline ul li.in-view::after {
  background: #3B2349;
}

.timeline ul li div {
  visibility: hidden;
  opacity: 0;
  transition: all .5s ease-in-out;
}

.timeline ul li:nth-child(odd) div {
  -webkit-transform: translate3d(200px, 0, 0);
          transform: translate3d(200px, 0, 0);
}

.timeline ul li:nth-child(even) div {
  -webkit-transform: translate3d(-200px, 0, 0);
          transform: translate3d(-200px, 0, 0);
}

.timeline ul li.in-view div {
  -webkit-transform: none;
          transform: none;
  visibility: visible;
  opacity: 1;
}


/* GENERAL MEDIA QUERIES
–––––––––––––––––––––––––––––––––––––––––––––––––– */

@media screen and (max-width: 900px) {
  .timeline ul li div {
    width: 250px;
  }

  .timeline ul li:nth-child(even) div {
    left: -289px;
    /*250+45-6*/
  }
}

@media screen and (max-width: 600px) {
  .timeline ul li {
    margin-left: 20px;
  }

  .timeline ul li div {
    width: calc(100vw - 91px);
  }

  .timeline ul li:nth-child(even) div {
    left: 45px;
  }

  .timeline ul li:nth-child(even) div::before {
    left: -15px;
    border-width: 8px 16px 8px 0;
    border-color: transparent #3B2349 transparent transparent;
  }
}
.project-holder {
  width: 70%;
  margin: auto;
  margin-bottom: 8%;
  background-color: #3B2349;
  padding: 12px;
}

.project-img {
  border-radius: 50%;
  margin: 10px;
  width: 240px;
  height: auto;
  transition: .8s;
}

.project-holder:nth-child(odd) .project-img {
  float: left;
}

.project-holder:nth-child(even) .project-img {
  float: right;
}

.project-title {
  font-size: 1.6rem;
}

.project-desc {
  font-size: 1.2rem;
}

.project-img:hover {
  transition: .4s;
  margin-top: -20px;
}

@media screen and (max-width: 992px) {
  .project-holder:nth-child(odd) .project-img {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: -30px;
  }

  .project-holder:nth-child(even) .project-img {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: -30px;
  }

  .project-title {
    text-align: center;
    font-size: 1.8rem;
  }
}
.meter {
    height: 50px;
    width: 65%;
    margin: auto;
    margin-bottom: 15px;
    position: relative;
    border-radius: 25px;
    padding: 5px;
    border: #3B2349 solid 5px;
}

.skill-filler {
    padding: 4px;
    padding-left: 12px;
    height: 100%;
    border-radius: 20px;
    background-color: #3B2349;
    -webkit-animation: hid 3.5s ease-out;
            animation: hid 3.5s ease-out;
}

.skill-filler.in-view {
    -webkit-animation: anim 2.6s ease-in-out;
            animation: anim 2.6s ease-in-out;
    visibility: visible;
}

@-webkit-keyframes anim {
    0% {
        width: 0px;
    }
}

@keyframes anim {
    0% {
        width: 0px;
    }
}

@-webkit-keyframes hid {
    0% {}

    100% {
        width: 0px;
    }
}

@keyframes hid {
    0% {}

    100% {
        width: 0px;
    }
}
